.notification {
    position: fixed;
    top: 15px;
    left: 50%;
    transform: translateX(-50%);
    width: 300px;
    z-index: 2;
    padding: 10px;
    background-color: rgb(106,115,141);
    border-radius: 5px;
    color: rgb(255, 255, 255);
}

@media (max-width: 550px) {
    .notification {
        font-size: 12px;
    }
}
