.chats {
    padding: 20px 0;

    &__heading {
        color: rgb(37, 202, 202);
        padding-left: 15px;
        margin-bottom: 20px;
    }

    &:last-child {
        padding-bottom: 0;
    }
}

@media (max-width: 768px) {
    .chats {
        padding: 15px 0;

        &__heading {
            padding-left: 10px;
            margin-bottom: 15px;
        }
    }
}

@media (max-width: 550px) {
    .chats {
        &__heading {
            font-size: 12px;
        }
    }
}
